.list {
    width: 100%;
    max-width: 850px;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 24px;
    margin-right: 8px;
    max-height: 80vh;
    overflow-y: scroll;
  }
  
  .list::-webkit-scrollbar {
    width: 8px;
    height: 664px;
    background-color: rgba(47, 47, 55, 1);
  }
  
  .list::-webkit-scrollbar-thumb {
    height: 492px;
    background-color: rgba(133, 133, 173, 1);
  }
  
  .list::-webkit-scrollbar-thumb:hover {
    background-color: rgb(99, 99, 128);
  }
  