.main_container {
    width: 100%;
    max-width: 600px;
  }
  
  .orders_container {
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 825px;
  }
  
  .list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  
  .orders_container::-webkit-scrollbar {
    width: 8px;
    height: 664px;
    background-color: rgba(47, 47, 55, 1);
  }
  
  .orders_container::-webkit-scrollbar-thumb {
    height: 492px;
    background-color: rgba(133, 133, 173, 1);
  }
  
  .orders_container::-webkit-scrollbar-thumb:hover {
    background-color: rgb(99, 99, 128);
  }