.wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    flex-direction: column;
    padding-top: 180px;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 88px;
    width: 480px;
  }
  .form div {
    width:100%
  }
  
  .form svg {
    margin-left: 190px;
  }
  
  .title{
    text-align: center;
  }
  
  .link {
    color: #4c4cff;
    text-decoration: none;
    transition: all 0.3s linear;
  }
  
  .link:hover {
    opacity: 0.8;
  }
  
  .link:active {
    color: white;
  }